"use client"

import Image from 'next/image'
import Header from '../components/Header'
import Footer from '../components/Footer'
import dynamic from 'next/dynamic'
import 'swiper/css'
import 'aos/dist/aos.css'
import { useEffect } from 'react'
import AOS from 'aos'
import { Swiper, SwiperSlide } from 'swiper/react'

export default function Home() {
  useEffect(() => {
    AOS.init({ duration: 1000 })
  }, [])

  return (
    <>
      <Header />
      <main>
        {/* Hero Section */}
        <section id="hero" className="bg-gray-100 py-20 text-center" data-aos="fade-up">
          <h1 className="text-5xl font-bold mb-4">CREATIVE DESIGN</h1>
          <p className="max-w-2xl mx-auto mb-6">Discover innovative strategies for impactful visual communication. We transform ideas into compelling realities, ensuring your brand stands out.</p>
          <a href="#services" className="bg-blue-500 text-white px-6 py-2 rounded">Explore Services</a>
        </section>

        {/* Services with Swiper */}
        <section id="services" className="py-20 bg-gray-50 px-4" data-aos="fade-up">
          <h2 className="text-4xl font-semibold text-center mb-10">Our Services</h2>
          <Swiper spaceBetween={30} slidesPerView={1} autoplay={{ delay: 3000 }}>
            <SwiperSlide>
              <div className="text-center p-4">Creative Branding - Unique brand identities</div>
            </SwiperSlide>
            <SwiperSlide>
              <div className="text-center p-4">Design Systems - Consistent UI/UX</div>
            </SwiperSlide>
            <SwiperSlide>
              <div className="text-center p-4">Marketing Strategies - Business Growth</div>
            </SwiperSlide>
          </Swiper>
        </section>

        {/* Portfolio Lightbox */}
        <section id="portfolio" className="py-20 px-4" data-aos="fade-up">
          <h2 className="text-4xl font-semibold text-center mb-10">Portfolio</h2>
          <div className="grid grid-cols-1 md:grid-cols-4 gap-4 max-w-5xl mx-auto">
            {[1,2,3,4].map(i => (
              <a href={`/assets/img/portfolio/portfolio-${i}.webp`} key={i} data-lightbox="portfolio">
                <Image src={`/assets/img/portfolio/portfolio-${i}.webp`} alt={`Portfolio ${i}`} width={300} height={200} />
              </a>
            ))}
          </div>
        </section>

        {/* Team Section */}
        <section id="team" className="py-20 bg-gray-50 px-4" data-aos="fade-up">
          <h2 className="text-4xl font-semibold text-center mb-10">Our Team</h2>
          <div className="grid grid-cols-1 md:grid-cols-3 gap-6 max-w-5xl mx-auto text-center">
            <div>
              <Image src="/assets/img/person/person-m-7.webp" alt="Walter White" width={150} height={150} className="rounded-full mx-auto" />
              <h3 className="font-bold mt-4">Walter White</h3>
              <p>CEO</p>
            </div>
            <div>
              <Image src="/assets/img/person/person-f-8.webp" alt="Sarah Johnson" width={150} height={150} className="rounded-full mx-auto" />
              <h3 className="font-bold mt-4">Sarah Johnson</h3>
              <p>Product Manager</p>
            </div>
            <div>
              <Image src="/assets/img/person/person-m-6.webp" alt="William Anderson" width={150} height={150} className="rounded-full mx-auto" />
              <h3 className="font-bold mt-4">William Anderson</h3>
              <p>CTO</p>
            </div>
          </div>
        </section>

        {/* FAQ Section */}
        <section id="faq" className="py-20 bg-gray-50 px-4" data-aos="fade-up">
          <h2 className="text-4xl font-semibold text-center mb-10">FAQ</h2>
          <div className="max-w-3xl mx-auto">
            <div className="mb-4">
              <h4 className="font-bold">What services do you offer?</h4>
              <p>Branding, Design Systems, Marketing, and more.</p>
            </div>
            <div className="mb-4">
              <h4 className="font-bold">How to contact us?</h4>
              <p>Via contact form or email at contact@agency.com</p>
            </div>
          </div>
        </section>

        {/* Contact Section */}
        <section id="contact" className="py-20 px-4" data-aos="fade-up">
          <h2 className="text-4xl font-semibold text-center mb-10">Contact Us</h2>
          <form className="max-w-xl mx-auto flex flex-col gap-4">
            <input type="text" placeholder="Name" className="border p-2 rounded" />
            <input type="email" placeholder="Email" className="border p-2 rounded" />
            <textarea placeholder="Your Message" className="border p-2 rounded" rows={4}></textarea>
            <button type="submit" className="bg-blue-500 text-white px-6 py-2 rounded">Send Message</button>
          </form>
        </section>
      </main>
      <Footer />
    </>
  )
}
